@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Open Sans', sans-serif;
}
.coin-container{
    display: flex;
    justify-content: center;
    width: 100%;
}
.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid grey;
    width: 100%;
    justify-content: space-between;
}
.coin{
    display: flex;
    margin: auto;
    align-items: center;
    padding-right: 24px;
    width: 80%;
    
}
.coin h1{
    font-size: 16px;
    width: 250px;
    align-self: center;
}
.coin-image img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin-data{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}

.coin-symbol{
    display: flex;
    justify-content: center;
    width: 200px;
}
.coin-price{
    display: flex;
    justify-content: center;
    width: 150px;
}
.coin-volume{
    display: flex;
    justify-content: center;
    width: 250px;
}
.coin-percent{
    display: flex;
    justify-content: center;
    width: 250px;
}
.red{
    color: red;
}
.green{
    color: green;
}
.coin-market-cap{
    width: auto;
}
:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1; /* Firefox */
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1; /* Firefox */
}
body{}
.App h1{
  display: flex;
  justify-content: center;
}

.coin-search form{
  display: flex;
  justify-content: center;
}
.coin-search form input{
  padding: 5px 15px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  color: #fff;
  background-image: linear-gradient(-225deg, #ac32e4 0%, #7918f2 40%, #4801ff 100%);
}
